/*wBreadcrumbs.scss*/
@import "../fonts";
@import "../_config";
.breadcrumbs {    
    width: 100%;
    padding-left: 20px;
    display: inline-block;
    vertical-align: middle;
    font-size: 0;
    line-height: 0;
    font-family: 'FuturaPT';
    font-weight: 300;
    box-sizing: border-box;
    > span {
        display: inline-block;
        vertical-align: top;
        font-size: 14px;
        line-height: 18px;
        // margin-right: 7px;
        color: $colorBlue;
        // text-decoration: underline;
        text-decoration: none;
        &:last-child {
            text-decoration: underline;
        }
        &:first-child:before {
            display: none;
        }
        > a {
            color: #c6c6c6;
            text-decoration: none;
            transition: color 0.2s linear;
            &:after {
                content: '>';
                display: inline-block;
                vertical-align: top;
                margin: 0 7px;
            }
            &:hover {
                color: $colorWhite;
            }
        }
    }
}
@media only screen and (max-width:620px) {
    .breadcrumbs {
        // display: block;
        > span {
            display: none;
            &:before {
                display: none;
            }
            &:nth-last-child(2) {
                display: block;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                max-width: 100%;
                background: none;
                margin: 0;
                &:before {
                    content: '<';
                    display: inline-block;
                    margin: 0 5px 0 3px;
                    color: #c6c6c6;
                }
            }
            > a:after {
                content: none;
            }
        }
    }

}